body {
  font-family: Arial, Helvetica, sans-serif;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}
a:link {
  text-decoration: none;
  color: black;
  text-align: center;
  justify-content: center;
}

a {
  color: black;
  text-align: center;
  justify-content: center;
}
.nav-link {
  color: black
}

.navbar-header {
  margin-right: auto;
  padding-top: 5%;
  font-size: xx-large;
} 
.navbar-brand {
font-size: xx-large;
padding-bottom: 20px;
}
li a {
  font-size: large;
}
@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: black;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
